
import './App.css';
import React,{useState,useEffect}from 'react';
import { toast, Toast,ToastContainer } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import Loader from 'react-loader-spinner';
import Container from '../components/Container/Container';
import data from '../data.json';
if(localStorage.getItem('data') === null){
  localStorage.setItem('data',JSON.stringify(data))  //kim tra xem localStorage c data khng, cha c th to localStorage
}
function App() {
  const [dataP,setData] = useState(JSON.parse(localStorage.getItem('data')));  //dataP l data ca localStorage
  const [loader,setLoader] = useState(false); //loader l state ca loader khi windows ang load
  useEffect(()=>{
    localStorage.setItem('data',JSON.stringify(dataP)) //lu dataP vo localStorage sau mi ln m dataP thay i
  },[dataP])
   //sttchange l state ca sttchange khi click vo nt thay i

  useEffect(() => {
   const timer = setTimeout(() => {
      setLoader(false); //khi windows load xong th set li loader = false
    },3000);
  }, [loader]);
  const handleAdd = (data) => {
      data = {...data,date: new Date(data.date)} //p kiu date
      setData(preState => [...preState,data]) //thm data vo dataP tuy nhin nn s dng chnh preState  
      toast.success('Data Added Successfully') //thng bo thm thnh cng
  }
  const handleDelete = (id) => {
    setData(preState => preState.filter(data => data.id !== id)) //xa data theo id s dng preState , mi khi state thay i c ph thuc vo state trc cn s dng preState
    toast.success('Data Deleted Successfully')
  }
  return (
    <> {window.addEventListener('load',()=>{ //lng nghe event load ca window
      setLoader(true);
    })}
      <div className='w-screen h-screen box-border bg-slate-600 flex items-center justify-center App'> 
       {
         loader?<Loader //nu loader = true th hin th loader
         type="Puff"
         color="#00BFFF"  
         height={100}
         width={100}
         margin="auto"
       />:
       <Container items={dataP} add={handleAdd} delete={handleDelete}></Container> 
       }
      </div>
      <ToastContainer //toast container
        position="top-right"
        autoClose={5000}
        hideProgressBar={false}
        newestOnTop={false}
        closeOnClick
        rtl={false}
        pauseOnFocusLoss
        draggable
        pauseOnHover
      />
      {/* Same as */}
      <ToastContainer />
    </>
  );
}

export default App;
